<template>
  <div>
    <div v-if="pageLoading" class="text-center">
      <i class="fa fa-spinner fa-spin fa-2x" />
    </div>
    <b-card id="personMgmtCard" v-else no-body>
      <b-tabs v-model="tabIndex">
        <b-tab
          :title-link-class="linkClass(0)"
          :title="$t('ACCOUNTINFORMATION')"
          @click="fetchAccountData"
        >
          <b-form>
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="3">
                <b-button
                  variant="primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{ $t("LOGINID") }}</label>
              </b-col>
              <b-col cols="12" xl="3">
                <b-form-input
                  v-model="managerAccount.loginID"
                  maxlength="15"
                  class="form-control"
                >
                </b-form-input>
                <show-errors
                  :control="$v.managerAccount.loginID"
                  :field="$t('LOGINID')"
                />
              </b-col>
            </b-row>
            <br />
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="3">
                <b-button
                  variant="primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("LOGINPASSWORD2")
                }}</label>
              </b-col>
              <b-col cols="12" xl="3">
                <b-form-input
                  id="input-pwd"
                 :disabled="person_mode_and_data.isEditmode"                  
                  v-model="managerAccount.password"
                  type="password"                  
                  class="form-control"
                  maxlength="15"
                ></b-form-input>
                <show-errors
                  :control="$v.managerAccount.password"
                  :field="$t('LOGINPASSWORD2')"
                />
              </b-col>
              <b-col cols="12" xl="3">
                <b-button
                  @click="resetPassword"
                  :disabled="!personData.personID"
                  variant="primary"
                  class="px-4 align-content-center"
                  >{{ $t("RESETPASSWORDBUTTON") }}
                  <i
                    v-if="resetPasswordLoading"
                    class="fa fa-spinner fa-spin"
                  />
                </b-button>
              </b-col>
            </b-row>
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="3"></b-col>
              <b-col cols="12" xl="3"></b-col>
              <b-col cols="12" xl="6" class="mt-2">
                {{ $t("PASSTEXT") }}
              </b-col>
            </b-row>
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="2">
                <b-button
                  variant="primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("ACCOUNTPERMISSIONS")
                }}</label>
              </b-col>
              <b-col xl="auto">
                <b-form-checkbox-group
                  id="checkbox-group-1"
                  v-model="managerAccount.accountAuth"
                  :options="authCategory"
                  class="small-fs"
                  size="md"
                ></b-form-checkbox-group>
                <show-errors
                  :control="$v.managerAccount.accountAuth"
                  :field="$t('ACCOUNTPERMISSIONS')"
                />
              </b-col>
            </b-row>
            <br />
            <b-row>
              <b-col cols="12" lg="3" xl="3">
                <b-button
                  v-if="!person_mode_and_data.isEditmode"
                  @click="toNextTab"
                  variant="primary"
                  class="px-4 align-content-center"
                  >{{ $t("TOPERSONINCHARGE") }}
                  <i v-if="accountLoader" class="fa fa-spinner fa-spin"
                /></b-button>
                <b-button
                  v-else
                  @click="addUpdateManagerAccount"
                  variant="primary"
                  class="px-4 align-content-center"
                  >{{ $t("UPDATE") }}
                  <i v-if="accountLoader" class="fa fa-spinner fa-spin"
                /></b-button>
              </b-col>
            </b-row>
            <br />
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="auto">
                <label class="highLightHeaderLabel">{{
                  $t("LOGINTIME")
                }}</label>
              </b-col>
              <b-col cols="12" xl="auto">
                <b-form-input
                  v-model="managerAccount.firstLoginDate"
                  class="form-control"
                  disabled
                ></b-form-input>
              </b-col>
              <b-col cols="12" xl="auto">
                <label class="highLightHeaderLabel">{{
                  $t("LOGINTIME2")
                }}</label>
              </b-col>
              <b-col cols="12" xl="auto">
                <b-form-input
                  v-model="managerAccount.lastLoginDate"
                  class="form-control"
                  disabled
                ></b-form-input>
              </b-col>
            </b-row>
          </b-form>
        </b-tab>
        <b-tab
          :title-link-class="linkClass(1)"
          :title="$t('PERSONINCHARGEINFO')"
          @click="fetchPersonData"
        >
          <b-form>
            <br />
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="2">
                <b-button
                  variant="primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("CONTACTNAME")
                }}</label>
              </b-col>
              <b-col cols="12" xl="2">
                <b-form-input
                  v-model="personData.personName"
                  maxlength="50"
                  class="form-control"
                ></b-form-input>
                <show-errors
                  :control="$v.personData.personName"
                  :field="$t('CONTACTNAME')"
                />
              </b-col>
              <b-col cols="12" xl="2">
                <b-button
                  variant="outline-primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("INPUTEXPENSEITEM.ANYBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{ $t("SEX") }}</label>
              </b-col>
              <b-col cols="12" xl="auto">
                <b-form-select
                  :plain="true"
                  v-model="personData.personSex"
                  :options="sexOptions"
                ></b-form-select>
              </b-col>
            </b-row>
            <br />
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="2">
                <b-button
                  variant="primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("AFFILIATEDCOMPANY")
                }}</label>
              </b-col>
              <b-col cols="12" xl="2">
                <b-form-select
                  :plain="true"
                  v-model="personData.personCompany"
                  :options="companyOptions"
                ></b-form-select>
                <show-errors
                  :control="$v.personData.personCompany"
                  :field="$t('AFFILIATEDCOMPANY')"
                />
              </b-col>
              <b-col cols="12" xl="2">
                <b-button
                  variant="outline-primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("INPUTEXPENSEITEM.ANYBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("DEPARTMENT")
                }}</label>
              </b-col>
              <b-col cols="12" xl="3">
                <b-form-input
                  v-model="personData.personDepartment"
                  maxlength="40"
                  class="form-control"
                ></b-form-input>
              </b-col>
              <b-col cols="12" xl="auto">
                <b-button
                  variant="outline-primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("INPUTEXPENSEITEM.ANYBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{ $t("POSITION") }}</label>
              </b-col>
              <b-col cols="12" xl="auto">
                <b-form-select
                  :plain="true"
                  v-model="personData.personPosition"
                  :options="positionOptions"
                ></b-form-select>
              </b-col>
            </b-row>
            <br />
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="2">
                <b-button
                  variant="primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("MAILADDRESS")
                }}</label>
              </b-col>
              <b-col cols="12" xl="3">
                <b-form-input
                  v-model="personData.personMailAddress"
                  class="form-control"
                ></b-form-input>
                <show-errors
                  :control="$v.personData.personMailAddress"
                  :field="$t('MAILADDRESS')"
                />
              </b-col>
            </b-row>
            <br />
            <b-row class="no-gutters d-flex align-items-center">
              <b-col cols="12" xl="2">
                <b-button
                  variant="outline-primary"
                  class="px-2 required-btnAny"
                  >{{ $t("ANYBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("PERSONINCHARGEICONIMAGE")
                }}</label>
              </b-col>
              <b-col cols="11" xl="3">
                <b-form-input
                  v-model="personData.personInchargeIcon"
                  placeholder="xxxxxx.png"
                  class="form-control"
                ></b-form-input>
                <p class="text-danger">{{ fileErrorMessage }}</p>
              </b-col>
              <b-col cols="12" xl="4">
                <b-button
                  id="fileSelect"
                  variant="primary"
                  class="file-selection-btn file-btn-margin"
                >
                  <label for="avatar">{{ $t("IMGSELUP") }}</label>
                </b-button>
                <input
                  id="avatar"
                  ref="fileupload"
                  @change="onFileChange"
                  type="file"
                  name="avatar"
                  hidden
                />
              </b-col>
            </b-row>
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="2"> </b-col>
              <b-col cols="11" xl="3">
                <b-link
                  @click="deleteIconFile"
                  href="javascript:;"
                  class="card-link float-left link mt-2"
                >
                  <u>{{ $t("PERSONINCHARGEICONIMAGEDEL") }}</u>
                </b-link>
              </b-col>
            </b-row>
            <br />
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="2">
                <b-button
                  variant="primary"
                  class="px-2 required-btnAny align-content-center"
                  >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("PERSONINCHARGEAUTHORITY")
                }}</label>
              </b-col>
              <b-col cols="12" xl="2">
                <b-form-select
                  :plain="true"
                  v-model="personData.personInchargeAuthority"
                  :options="authorityOptions"
                ></b-form-select>
                <show-errors
                  :control="$v.personData.personInchargeAuthority"
                  :field="$t('PERSONINCHARGEAUTHORITY')"
                />
              </b-col>
            </b-row>
            <br />
            <b-row>
              <b-col cols="12" lg="3" xl="3">
                <b-button
                  @click="personSubmit"
                  variant="primary"
                  class="px-4 align-content-center"
                  >{{ buttonSelect
                  }}<i v-if="personLoader" class="fa fa-spinner fa-spin"
                /></b-button>
              </b-col>
            </b-row>
          </b-form>
        </b-tab>
      </b-tabs>
    </b-card>
    <b-modal
      id="client-modal1"
      ref="client-modal1"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="lg"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
    >
      <template>
        <div class="float-left">
          <b-button
            @click="goBack"
            class="back-btn close-btn mt-2"
            variant="primary"
            >{{ $t("BACK") }}</b-button
          >
        </div>
        <br />
        <div>
          <br />
          <br />
          <label class="title-fs font-weight-bold"
            >{{ $t("MailMessage1") }}{{ personData.personMailAddress
            }}{{ $t("MailMessage2") }}</label
          >
        </div>
        <br />
        <div class="title-fs font-weight-bold txt-align">
          <label
            >{{ $t("LOGINID") }} :
            {{ managerAccount.loginID ? managerAccount.loginID : "" }}</label
          ><br />
          <label
            >{{ $t("PERSONINCHARGENAME") }} : {{ personData.personName }}</label
          >
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import {
  required,
  email,
  minLength,
} from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { getDateHourMinute } from "@/shared/utils";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "AddEdit",
  components: { ShowErrors },
  data() {
    return {
      isEditMode: false,
      tabIndex: 0,
      fileName: "",
      selectedSex: "",
      accountLoader: false,
      personLoader: false,
      pageLoading: false,
      fileErrorMessage: "",
      buttonSelect: this.$t("SAVE"),
      sexOptions: [
        { value: "00", text: "指定なし" },
        { value: "01", text: "男性" },
        { value: "02", text: "女性" },
      ],
      selectedCompany: "",
      companyOptions: [
        { value: "1", text: "企業１" },
        { value: "2", text: "企業２" },
      ],
      selectedPosition: "",
      positionOptions: [
        { value: "001", text: "一般" },
        { value: "002", text: "係長" },
        { value: "003", text: "課長" },
        { value: "004", text: "部長" },
        { value: "005", text: "役員" },
      ],
      selectedAuthority: "",
      authorityOptions: [
        { value: "01", text: "一般" },
        { value: "02", text: "管理者" },
      ],
      authCategory: [
        { value: "1", text: this.$t("WIZOPERATOR") },
        { value: "2", text: this.$t("SUPPLIERREPRESENTATIVE") },
        { value: "3", text: this.$t("SYSTEMADMINISTRATOR") },
      ],
      managerAccount: {
        aplManagerAccountNo: "",
        loginID: "",
        password: "",
        accountAuth: [],
        firstLoginDate: "",
        lastLoginDate: "",
      },
      personData: {
        personID: "",
        aplManagerAccountNo: "",
        personName: "",
        personSex: "",
        personCompany: "",
        personDepartment: "",
        personPosition: "",
        personMailAddress: "",
        personInchargeIcon: "",
        personInchargeAuthority: "",
        fileIconPath: "",
      },
      files: [],
      resetPasswordLoading: false,
    };
  },
  validations: {
    personData: {
      personName: { required },
      personCompany: { required },
      personMailAddress: { required, email },
      personInchargeAuthority: { required },
    },
    managerAccount: {
      loginID: {
        required,
        characterMinLength: minLength(8),
      },
      password: { required, minLength: minLength(8) },
      accountAuth: { required },
    },
  },
  computed: {
    ...mapGetters({
      person_mode_and_data: "getPersonDataAndMode",
    }),
  },
  async mounted() {
    this.personData.personID = this.person_mode_and_data.person_id;
    this.isEditMode = this.person_mode_and_data.isEditmode;
    this.getAffliationCompany();
    await this.fetchPersonData();
    this.fetchAccountData();
  },
  methods: {
    /**
     * @description function to check the account auth
     * @name getPersonAuth
     * @param check
     * @returns boolean
     */
    getPersonAuth(check) {
      let fetchData = this._.filter(this.managerAccount.accountAuth, (item) => {
        return item == check;
      });
      if (fetchData.length) {
        return true;
      } else {
        return false;
      }
    },
    /**
     * @description function to delete the icon file
     * @name deleteIconFile
     * @param none
     * @returns nothing
     */
    async deleteIconFile() {
      this.fileErrorMessage = "";
      this.files = [];
      if (this.personData.personID) {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.PERSONDELETEUPLOADEDFILE,
          {
            person_id: this.personData.personID,
          }
        );
        if (data.success) {
          this.personData.personInchargeIcon = "";
          this.$refs.fileupload.value = null;
        }
      } else {
        this.personData.personInchargeIcon = "";
        this.$refs.fileupload.value = null;
      }
    },
    /**
     * @description function to reset the password
     * @name resetPassword
     * @param none
     * @returns nothing
     */
    async resetPassword() {
      this.resetPasswordLoading = true;
      const { data } = await BasicInfoApis.postAPI(endPoint.MFORGOTPASSWORD, {
        remind_mail: this.personData.personMailAddress,
      });
      this.resetPasswordLoading = false;

      if (data.success) {
        this.$refs["client-modal1"].show();
      }
    },
    /**
     * @description function to hide the modal popup
     * @name goBack
     * @param none
     * @returns nothing
     */
    goBack() {
      this.$refs["client-modal1"].hide();
    },
    /**
     * @description function to switch to next tab
     * @name toNextTab
     * @param none
     * @returns nothing
     */
    async toNextTab() {
      this.$v.managerAccount.$touch();
      if (!this.$v.managerAccount.$invalid) {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.MASTERREADDATAAPLMANAGERACCOUNT,
          {
            fieldlist: ["*"],
            conditions: [
              {
                key: "apl_manager_login_id",
                value: this.managerAccount.loginID,
              },
            ],
          }
        );
        if (data.data.length > 0) {
          this.$swal({
            type: "warning",
            width: 425,
            text: this.$t("LOGINIDALREADYEXISTS"),
          });
        } else {
          this.tabIndex = 1;
        }
      }
    },
    /**
     * @description function to add or update the manager account
     * @name addUpdateManagerAccount
     * @param none
     * @returns nothing
     */
    async addUpdateManagerAccount() {
      this.$v.managerAccount.$touch();
      if (!this.$v.managerAccount.$invalid) {
        this.accountLoader = true;
        let updationAccountAPI = {
          apl_manager_login_id: this.managerAccount.loginID,
          del_flg: false,
          create_id: localStorage.getItem("personID"),
          update_id: localStorage.getItem("personID"),
          update_dt: getDateHourMinute(new Date()),
          wiz_auth_flg: this.getPersonAuth("1"),
          supplier_auth_flg: this.getPersonAuth("2"),
          system_auth_flg: this.getPersonAuth("3"),
          apl_manager_account_no: parseInt(
            this.managerAccount.aplManagerAccountNo
          ),
        };
        if (this.managerAccount.aplManagerAccountNo != "") {
          const { data } = await BasicInfoApis.postAPI(
            endPoint.UPDATEAPLMANAGERACCOUNT,
            updationAccountAPI
          );
          this.accountLoader = false;
          if (data.success) {
            this.$router.push({ name: "personincharge_list" });
          } else {
            if (data.message) {
              this.$swal({
                type: "warning",
                width: 425,
                text: this.$t("LOGINIDALREADYEXISTS"),
              });
            }
          }
        }
      }
    },
    /**
     * @description function to get the ffiliation company
     * @name getAffliationCompany
     * @param none
     * @returns nothing
     */
    async getAffliationCompany() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMGENERALPURPOSE,
        {
          fieldlist: ["code", "value_string_small"],
          conditions: [
            {
              key: "key",
              value: "AFFI",
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        let companyName = [];
        responseData.forEach((item) => {
          let options = {
            value: item.code,
            text: item.value_string_small,
          };
          companyName.push(options);
        });
        this.companyOptions = companyName;
      }
    },
    /**
     * @description function to update the auth
     * @name updateAuth
     * @param flagData
     * @returns array 'auth array'
     */
    updateAuth(flagData) {
      let flagArray = [];
      flagData.wiz_auth_flg ? flagArray.push("1") : "";
      flagData.supplier_auth_flg ? flagArray.push("2") : "";
      flagData.system_auth_flg ? flagArray.push("3") : "";
      return flagArray;
    },
    /**
     * @description fucntion to get the account data
     * @name fetchAccountData
     * @param none
     * @returns nothing
     */
    async fetchAccountData() {
      if (this.personData.personID) {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.MASTERREADDATAAPLMANAGERACCOUNT,
          {
            fieldlist: ["*"],
            conditions: [
              {
                key: "apl_manager_account_no",
                value: this.managerAccount.aplManagerAccountNo,
              },
            ],
          }
        );
        if (data.success) {
          let dataLength = data.data.length;
          if (dataLength > 0) {
            let responseData = data.data[0];
            this.managerAccount = {
              aplManagerAccountNo: responseData.apl_manager_account_no
                ? responseData.apl_manager_account_no
                : "",
              loginID: responseData.apl_manager_login_id
                ? responseData.apl_manager_login_id
                : "",
              password: "********",
              accountAuth: responseData ? this.updateAuth(responseData) : [],
              firstLoginDate: responseData.first_login_dt
                ? responseData.first_login_dt.slice(0, 16)
                : "",
              lastLoginDate: responseData.first_login_dt
                ? responseData.last_login_dt.slice(0, 16)
                : "",
            };
          }
        }
      }
    },
    /**
     * @description function to get the person data
     * @name fetchPersonData
     * @param none
     * @returns nothing
     */
    async fetchPersonData() {
      if (this.personData.personID) {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.MASTERREADMPERSON,
          {
            fieldlist: ["*"],
            conditions: [
              {
                key: "person_id",
                value: this.personData.personID,
              },
            ],
          }
        );
        if (data.success) {
          let dataLength = data.data.length;
          if (dataLength > 0) {
            let responseData = data.data[0];
            this.personData = {
              personID: responseData.person_id ? responseData.person_id : "",
              aplManagerAccountNo: responseData.apl_manager_account_no
                ? responseData.apl_manager_account_no
                : "",
              personName: responseData.person_name
                ? responseData.person_name
                : "",
              personSex: responseData.person_sex ? responseData.person_sex : "",
              personCompany: responseData.person_company
                ? responseData.person_company
                : "",
              personDepartment: responseData.person_department
                ? responseData.person_department
                : "",
              personPosition: responseData.person_position
                ? responseData.person_position
                : "",
              personMailAddress: responseData.person_mail
                ? responseData.person_mail
                : "",
              personInchargeIcon: responseData.icon_file_name
                ? responseData.icon_file_name
                : "",
              personInchargeAuthority: responseData.person_auth_kbn
                ? responseData.person_auth_kbn
                : "",
              fileIconPath: responseData.icon_file_name
                ? responseData.icon_file_name
                : "",
            };
            this.managerAccount.aplManagerAccountNo = responseData.apl_manager_account_no
              ? responseData.apl_manager_account_no
              : "";
            this.buttonSelect = this.$t("UPDATE");
          } else {
            this.buttonSelect = this.$t("SAVE");
          }
        } else {
          this.buttonSelect = this.$t("SAVE");
        }
      }
    },
    /**
     * @description function which triggers file change in the file control
     * @name onFileChange
     * @param event
     * @returns nothing
     */
    onFileChange(event) {
      let fileSize = 0;
      for (let i = 0; i < event.target.files.length; i++) {
        var ext = event.target.files[i].name.split(".").pop();
        fileSize += Math.round(event.target.files[i].size / 1024);
        if (
          ext == "jpg" ||
          ext == "png" ||
          ext == "jpeg" ||
          ext == "JPG" ||
          ext == "PNG" ||
          ext == "JPEG"
        ) {
          if (fileSize > 1024) {
            this.fileErrorMessage = this.$t("MAXICONSIZE");
          } else {
            for (let i = 0; i < event.target.files.length; i++) {
              this.files.push(event.target.files[i]);
            }
            this.personData.personInchargeIcon = this.files[0].name;
            this.personData.fileIconPath = this.files[0].name;
            this.fileErrorMessage = "";
          }
        } else {
          this.fileErrorMessage = this.$t("UPLOADEXCECUTABLEFILES");
        }
        fileSize += Math.round(event.target.files[i].size / 1024);
      }
    },
    /**
     * @description function to register the person in db
     * @name personSubmit
     * @param none
     * @returns nothing
     */
    async personSubmit() {
      this.$v.personData.$touch();

      //for create
      if (!this.isEditMode) {
        this.$v.managerAccount.$touch();
        if (this.$v.managerAccount.$invalid) {
          this.tabIndex = 0;
          return false;
        }
      }

      if (!this.$v.personData.$invalid) {
        var formData = new FormData();
        let url = endPoint.PERSONCREATENUPDATEPERSON;
        this.personLoader = true;

        //common payload (required fields)

        formData.append("person_name", this.personData.personName);
        formData.append("person_company", this.personData.personCompany);
        formData.append("person_mail", this.personData.personMailAddress);
        formData.append("person_sex", this.personData.personSex);
        formData.append("person_department", this.personData.personDepartment);
        formData.append("person_position", this.personData.personPosition);
        formData.append(
          "person_auth_kbn",
          this.personData.personInchargeAuthority
        );

        formData.append("icon_file_name", this.personData.personInchargeIcon);
        if (this.files.length > 0) {
          formData.append("attachment", this.files[0]);
        }
        formData.append("create_id", localStorage.getItem("client_account_no"));
        formData.append("update_id", localStorage.getItem("client_account_no"));
        formData.append("update_dt", getDateHourMinute(new Date()));

        if (this.isEditMode) {
          //update mode
          formData.append("person_id", this.personData.personID);
          formData.append(
            "apl_manager_account_no",
            this.personData.aplManagerAccountNo
          );
        } else {
          //create mode
          formData.append("apl_manager_login_id", this.managerAccount.loginID);
          formData.append(
            "apl_manager_login_pass",
            this.managerAccount.password
          );
          formData.append("wiz_auth_flg", this.getPersonAuth("1"));
          formData.append("supplier_auth_flg", this.getPersonAuth("2"));
          formData.append("system_auth_flg", this.getPersonAuth("3"));
          formData.append("del_flg", false);
          formData.append("create_dt", getDateHourMinute(new Date()));
        }

        const { data } = await BasicInfoApis.postAPI(url, formData);
        this.personLoader = false;
        if (data.success) {
          this.$router.push({ name: "personincharge_list" });
          this.fileErrorMessage = "";
        } else {
          this.fileErrorMessage = "";
          if (data.message) {
            this.$swal({
              type: "warning",
              width: 425,
              text: data.message.info,
            });
          }
        }
      }
    },
    /**
     * @description function to change the tab color based on the index of the tab
     * @name linkClass
     * @param idx
     * @returns array 'array of css classes'
     */
    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ["bg-white", "text-black"];
      } else {
        return ["bg-primary", "text-black"];
      }
    },
  },
};
</script>
